import React, { useContext, useState, useEffect } from 'react'
import { Box, Image, Heading, Text, ResponsiveContext, Table, TableHeader, TableRow, TableCell, TableBody, Form, Select, Accordion, AccordionPanel } from 'grommet';
import Hero from '../../../Components/Hero/Hero';
import BackOfficeDashboardImage from "../../../assets/images/dark-dashboard.jpg";
import EmptyImage from "../../../assets/illustrations/eastwood-no-messages.png";
import styled from 'styled-components';
import { IUserAccount, DateRange, ITransaction } from '../../../Types';
import Axios, { Canceler } from 'axios';
import Claims from '../Claims/Claims';
import ProgressBar from '../../../Components/ProgressBar/ProgressBar';
import PatientInput from '../../../Components/_Grommet/Inputs/PatientInput';
import CalendarDropDown from '../../../Components/_Grommet/Selects/Calendar';
import RangeCalendar from '../../../Components/_Grommet/Selects/RangeCalendar';
import spinner from "../../../assets/svg/spinner.svg";
import "./UserView.css";
import SlimSpinner from '../../../Components/_Grommet/Spinners/SlimSpinner/SlimSpinner';
import Popup from 'reactjs-popup';
import Scrollbars from 'react-custom-scrollbars';
import moment from 'moment';
import BankList from "../../../_data/banks.json";



const Wrapper = styled(Box)`
	width: 100vw;
	align-items: center;
	padding-bottom: 2rem;
	@media (min-width: 768px) {
		// align-items: start;
	}
`;

const UserView = () => {
	const size = useContext(ResponsiveContext);
	const [loading, setLoading] = useState(false);
	const [users, setUsers] = useState([] as IUserAccount[]);
	const [currentUser, setCurrentUser] = useState({} as IUserAccount);
	const [currentUserTransactions, setCurrentUserTransactions] = useState([] as ITransaction[]);

	const [popupOpen, setPopupOpen] = useState(false);
	const [loadingPopup, setLoadingPopup] = useState(false);
	const [loadingUserTransactions, setLoadingUserTransactions] = useState(false);
	const [loadingUserBankAccounts, setLoadingUserBankAccounts] = useState(false);
	

	const [dateRange, setDateRange] = useState({start: new Date(), end: new Date()} as DateRange);
	const [searchQuery, setSearchQuery] = useState("");

	let cancelRequest: Canceler;
	let _loading: boolean;
	let _refreshRequired: boolean;
	let eventHandled: boolean;

	useEffect(() => {
        getUsers();
	}, [searchQuery, dateRange]);





	const getUsers = () => {
		if (_loading) {
			_refreshRequired = true;
			cancelRequest();
			return;
		}

		_loading = true;
		setLoading(true);
		

		Axios.get<IUserAccount[]>("/api/backOffice/accounts", { params: { start: dateRange.start, end: dateRange.end, searchQuery },
			cancelToken: new Axios.CancelToken((c) => cancelRequest = c) })
			.then((res) => {
				if (res.status == 200) {
					const data = res.data.sort((a, b) => {
						var dateA = new Date(a.created), dateB = new Date(b.created);
						return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
					})

					setUsers(data);
				}
			}).catch((err) => {
				console.log(err);
			}).finally(() => {
				_loading = false;

				if (_refreshRequired) {
					_refreshRequired = false;
					getUsers();
				} else {
					setTimeout(() => setLoading(false), 1000);
				}
			})
	}

	const getUserTransactions = (id: string) => {
		setLoadingPopup(true);

		Axios.get<ITransaction[]>("/api/backOffice/accounts/single/transactions"
			, { params: { id }})
			.then((res) => {
				if (res.status == 200) {
					const data = res.data.sort((a, b) => {
						var dateA = new Date(a.addedAt), dateB = new Date(b.addedAt);
						return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
					})
					
					setCurrentUserTransactions(data);
				}
			}).catch((err) => {
				console.log(err);
			}).finally(() => {
				setLoadingPopup(false);
			});
	}

	const getFullUserData = (id: string) => {
		setLoadingPopup(true);

		Axios.get<IUserAccount>("/api/backOffice/accounts/single", { params: { id }})
			.then((res) => {
				if (res.status == 200) {
					setCurrentUser(res.data);
					console.log(res.data);
				}
			}).catch((err) => {
				console.log(err);
			}).finally(() => {
				getUserTransactions(id);
			});
	}

	return (
		<Wrapper width="10vw" pad={{bottom: "2rem"}} align="center">
			<Hero
				image={BackOfficeDashboardImage}
				text="Users"
			/>

			<Popup position="center center" open={popupOpen} modal
				closeOnDocumentClick={true}
				onClose={() => setPopupOpen(false)}
                contentStyle={{
                    width: size === "small" ? "60%" : "700px",
                    height: size === "small" ? "200px" : "700px",
                    borderRadius: "20px",
                    boxShadow: "6px 7px 13px -3px rgba(0,0,0,0.5)"
                }}
            >
                <Box width="100%" height="100%">
					<Box pad="30px" align="center" width="100%" height="100%" 
						justify="between">
							{loadingPopup ? (
								<Image src={spinner} width="100px" margin="auto"/>
							) : (
								<Box width="100%" height="100%">
									<Scrollbars autoHide>
										<Box direction="column" justify="start">
											<Box direction="row" align="start">
												<Box background="#444" width="150px" height="150px" align="center" justify="center">
													<Text color="#fff" weight="bold" size="50px">
														{currentUser.firstName && currentUser.lastName ? `${currentUser.firstName.substr(0,1)}${currentUser.lastName.substr(0,1)}` : ""}
													</Text>
												</Box>

												<Box alignSelf="end" margin={{left: "medium"}}>
													<Text size="35px">{`${currentUser.firstName} ${currentUser.lastName}`}</Text>
													<Text weight="bold">{currentUser.email}</Text>
													<Text weight={300}>{currentUser.phoneNumber}</Text>
												</Box>
											</Box>

											<Box margin={{top: "30px"}}>
												<Box direction="row">
													<Box width="150px">
														<Text size="16px">Created On:</Text>
													</Box>
													<Text>{moment(currentUser.created).format("lll")}</Text>
												</Box>

												<Box direction="row">
													<Box width="150px">
														<Text size="16px">State Of Origin:</Text>
													</Box>
													<Text>{currentUser.stateOfOrigin}</Text>
												</Box>

												<Box direction="row">
													<Box width="150px">
														<Text size="16px">Date Of Birth:</Text>
													</Box>
													<Text>{moment(currentUser.dateOfBirth).format("l")}</Text>
												</Box>

												<Box direction="row">
													<Box width="150px">
														<Text size="16px">Available Balance:</Text>
													</Box>
													<Text>{currentUser.balance ? "₦ "  + currentUser.balance.toLocaleString() : "₦ 0"}</Text>
												</Box>
											</Box>

											<Box width="500px" margin={{top: "50px", left: "10px"}}>
												<Accordion>
													<AccordionPanel label="Transactions">
														{
															currentUserTransactions && currentUserTransactions.length > 0 ? (
																<>
																	{currentUserTransactions.map((t, i) => (
																		<Box className="hoverBar" key={i} gap="medium" direction="row" pad="small">
																			<Box width="150px">
																				<Text>{moment(t.addedAt).format("ll")}</Text>
																			</Box>
																			
																			<Box width="100px">
																				<Text>{t.amount.toLocaleString()}</Text>
																			</Box>
																			
																			<Box width="100px">
																				<Text>{t.type}</Text>
																			</Box>
																		</Box>
																	))}
																</>
															) : (
																<Text style={{opacity: .7}}>There does not seem to be any transactions available for display</Text>
															)
														}
													</AccordionPanel>

													<AccordionPanel label="Bank Accounts">
														{
															currentUser.bankAccounts && currentUser.bankAccounts.length > 0 ? (
																<>
																	{currentUser.bankAccounts.map((b, i) => {
																		let bank = BankList[b.bankId - 1];
																		return (
																		<Box className="hoverBar" key={i} gap="medium" direction="row" pad="small">
																			<Box width="150px">
																				<Text>{b.accountName}</Text>
																			</Box>

																			<Box width="100px">
																				<Text>{b.accountNumber}</Text>
																			</Box>

																			<Box direction="row" width="100px" align="end" justify="center" gap="small">
																				<Image width="20px" src={bank.logo} />
																				<Text>{bank.knownAs}</Text>
																			</Box>
																		</Box>
																	)})}
																</>
															) : (
																<Text style={{opacity: .7}}>There does not seem to be any bank accounts available for display</Text>	
															)
														}
													</AccordionPanel>
												</Accordion>
											</Box>
										</Box>
									</Scrollbars>
								</Box>
							)}
					</Box>
                </Box>
            </Popup>

			
			<Box justify="center" align="center">


			<Box
                    pad="large"
                    width={size !== "small" ? "90vw" : "80vw"}
                    background="white"
                    overflow={{ horizontal: "auto" }}
                    align="center"
                    justify="center"
                    round="small"
					elevation="medium"
					margin={{top: "large"}}
                >
					<Box width="100%">
						<Box width="100%">
							<Form>
								<Box width="100%" direction="row" margin={{vertical: "small"}} justify="between">
									<RangeCalendar onChange={(range: { start: Date, end: Date}, target: RangeCalendar) => {
										setDateRange({start: range.start, end: range.end});
									}} />

									<Box direction="row" gap="2px"
                                         pad={{top: "small"}} alignSelf="end"
                                    >
										<Box width="32px" color="#9060EB" alignSelf="center">
											<i className="zwicon-search" style={{color: "#9060EB"}} />
										</Box>

										<PatientInput waitInterval={3000} 
											placeholder="Search" value={searchQuery} onChange={(event) => {  setSearchQuery(event.target.value); }}/>
									</Box>
								</Box>
							</Form>
						</Box>
					</Box>

					<Box width="100%" style={{position: "relative", minHeight: "500px"}} margin={{top: "30px"}}>
						{!loading && (!users || users.length <= 0) ? (
							<Box height="100%" width="100%" 
								align="center" justify="center"
								style={{
									position: "absolute",
									top: 0, left: 0,
								}}
							>
								<Box height="300px" width="auto">
									<Image src={EmptyImage} fit="contain"/>
								</Box>

								<Box width="500px" align="center" justify="center">
									<Heading margin={{top: "0px"}} level="4">Huh? What happened to the Postman?</Heading>
									<Text weight={400} size="16px" textAlign="center">
										There doesn't seem to be any data to display,
                                        try searching or filtering to find
										what you want.
									</Text>
								</Box>
							</Box>
						) : null}
						
						<SlimSpinner active={loading}/>
						
						<Table cellPadding="medium" className="hoverTable">
							<TableHeader
								style={{
									borderBottom: "solid 1px #ccc",
									fontSize: "14px !important",
								}}
							>
								<TableRow>
									<TableCell scope="col">
										Created
									</TableCell>
									<TableCell scope="col">
										Name
									</TableCell>
									<TableCell scope="col">
										Phone Number
									</TableCell>
									<TableCell scope="col">
										Email
									</TableCell>
								</TableRow>
							</TableHeader>

							<TableBody>
								{users.map((user, index, array) => (
									<TableRow
										key={index}
										style={{
											padding: ".1rem"
										}}
										onClick={(event) => {
											if (eventHandled) {
												eventHandled = false;
												return;
											}

											setCurrentUser(user);
											getFullUserData(user.id);
											getUserTransactions(user.id);
											setPopupOpen(true);
											//set
										}}
									>
										<TableCell>
											{moment(user.created).format("lll")}
										</TableCell>

										<TableCell>
											{user.firstName + " " + user.lastName}
										</TableCell>

										<TableCell>
											{user.phoneNumber}
										</TableCell>

										<TableCell>
											{user.email}
										</TableCell>
									</TableRow>
								))}
							</TableBody>
						</Table>
					</Box>
				</Box>
			</Box>
			
		</Wrapper>
	)
}

export default UserView;
